<script>
// @ is an alias to /src
import axios from 'axios'
export default {
  name: "diary",
  data() {
    return {
      res: null
    }
  },
  created() {
    axios.get("http://xiaolongwu.cn/yinuo/diary.json").then((res) => {
        this.res = res.data.list
    })
  }
}
